import { gameWidth, gameHeight, playerRadius, sensitivity, gameOverTrack } from './props.js'

let $gameHeight;
let $sensitivity;
export class Player {
	constructor(context, image, x, y)
	{
		playerRadius.subscribe(v => { this.radius = v })
		gameHeight.subscribe(v => { $gameHeight = v })
		sensitivity.subscribe(v => { $sensitivity = v })


		this.context = context
		this.image = image
		this.x = x
		this.y = y
		this.crashed = false
	}
	update(volume) {
		let breakpoint = 12

		// apply volume raise
		this.y -= (volume - breakpoint) / 4

		// edge controls
		if( (this.y + (this.radius*2)) > $gameHeight )
			this.y = $gameHeight- (this.radius*2)
		else if( this.y < 0 )
			this.y = 0

		this.draw()
	}
	draw() {
		this.context.drawImage(this.image, this.x, this.y, this.radius*2, this.radius*2)
		// this.image.onload = () => {
		// 	this.context.drawImage(this.image, 0, 0, this.image.width, $gameHeight)
		// }

		// if( this.image.naturalWidth == 0 )
		// {
			// console.log(1)
			
			// this.context.strokeRect(this.x, this.y, this.radius*2, this.radius*2)
		// }

		// this.context.beginPath()
		// this.context.arc(this.x, this.y, this.radius, 0, 2 * Math.PI)
		// this.context.fillStyle = 'lightblue'
		// this.context.fill()
		// this.context.stroke()
	}
	crash()
	{
		if( !this.crashed )
		{
			this.crashed = true
		}
	}
	edges()
	{
		return {
			'left': this.x,
			'right': this.x + (this.radius * 2),
			'top': this.y,
			'bottom': this.y + (this.radius * 2)
		}

		// return {
		// 	'left': this.x - this.radius,
		// 	'right': this.x + this.radius,
		// 	'top': this.y - this.radius,
		// 	'bottom': this.y + this.radius
		// }
	}
}
