import { getContext, onMount, onDestroy } from 'svelte';
import { writable, derived } from 'svelte/store'

// Some props for the app
export const version = writable('0.94b')
export const gameLang = writable('tr')
export const pixelRatio = writable(window.devicePixelRatio)
export const score = writable(0)
export const blocks = writable({})
export const gameWidth = writable(window.innerWidth * 0.7);
export const gameHeight = writable(window.innerHeight * 0.7);
export const volumeCoefficient = writable(1000);
export const sensitivity = writable(1);
export const playerRadius = derived(pixelRatio, $pixelRatio => {
	return 20 * (1/$pixelRatio)
	// return $gameWidth / 50
})
export const pixelRatioCoefficient = derived(pixelRatio, $pixelRatio => {
	return (1/$pixelRatio)
})
export const gameOverTrack = () => {
	return new Audio('game_over.wav')
};

export const canShare = () => {
	return !!(navigator.share);
}
export const shareMyScore = (score, level) => {
	if (navigator.share) {
		navigator.share({
			title: `FlappyDirt`,
			text: `Hey, I did ${score} on FlappyDirt! Can you beat me?`,
			url: 'https://alabacakmurat.github.io/flappydirt/',
		})
			.then(() => console.log('Successful share'))
			.catch((error) => console.log('Error sharing', error));
	}
}


export const level = derived(score, $score => Math.ceil(Math.max($score, 1) / 5))
// export const level = derived(score, $score => $score+1)