import { gameLang } from './props.js'
import {default as Turkish} from './lang/tr.js'
import {default as English} from './lang/en.js'

let $gameLang;
export function lang(key, props) {
	gameLang.subscribe(v => {$gameLang = v})

	if( $gameLang == 'tr' )
		var line = Turkish[key] || '';
	else
		var line = English[key] || '';

	if( typeof props == 'object' )
	{
		Object.keys(props).forEach(key => {
			line = line.replace('{'+key+'}', (props[key]))
		})
	}

	return line
} 