import { gameWidth, gameHeight, pixelRatio, pixelRatioCoefficient, playerRadius, score, level, blocks } from './props.js'

let $gameWidth
let $gameHeight
let $playerRadius
let $score
let $pixelRatio
let $pixelRatioCoefficient
let $level
let lastBlockDeployedAt = Date.now()
export class Block {
	constructor(context, _level, gap, width, height)
	{
		gameWidth.subscribe(v => { $gameWidth = v })
		gameHeight.subscribe(v => { $gameHeight = v })
		playerRadius.subscribe(v => { $playerRadius = v})
		score.subscribe(v => { $score = v})
		level.subscribe(v => { $level = v})

		if( !height )
		{
			let percentage = Math.floor(Math.random() * 100) + 1
			height = Math.max(10, (($gameHeight / 2) * percentage) / 100)
		}

		this.context = context
		// this.speed = ((10*1) + 0.6)
		this.speed = (($level*0.4) +1.2)
		console.log($level)
		// this.speed = (1.8)

		this.blockWidth = width || 100
		this.blockGap = Math.max((this.gap || 0), this.calculateGap($level))
		this.blockHeight = height || 50

		// re-arrange the height if it exceeds
		if( (this.blockHeight + this.blockGap) >= $gameHeight )
		{
			this.blockHeight -= (this.blockHeight / 2)
			// this.blockHeight = $gameHeight - (this.blockHeight + this.blockGap) - 10
		}

		this.xPosition = $gameWidth + 10
		// this.xPosition = $gameWidth / 2
		this.topSpace = this.blockHeight + this.blockGap


		const colors = ["darkgreen", "darkblue", "darkred", "brown", "black"];

		this.randomColor = colors[$level - 1] || 'black';

		this.dead = false
		this.passed = false

		this.prevBlock = false
	}
	update(player) {

		let playerEdges = player.edges()
		if( !this.dead && (this.xPosition + this.blockWidth) > 0 )
		{
			this.xPosition -= this.speed
			this.draw()

			// let's check if the player is behind us
			if( !this.passed && (this.xPosition + this.blockWidth) < playerEdges.right )
			{
				this.passed = true

				score.update(v => v+1)
			}

			// Is the player inside the block wrapper?
			if( playerEdges.right >= this.xPosition && playerEdges.left <= (this.xPosition+this.blockWidth) )
			{
				// The player is inside the wrapper
				// If it is outside the gap, it will crash
				if( playerEdges.top <= this.blockHeight || playerEdges.bottom >= this.topSpace )
				{
					player.crash()
				} 
			}
		} else {
			this.dead = true
		}
	}
	draw() {
		this.context.fillStyle = this.randomColor



		// create the top block in the middle of the game
		this.context.fillRect(this.xPosition, 0, this.blockWidth, this.blockHeight)
		// create the derived bottom block
		// starting pos = topBlockHeight + gap
		// height = gameHeight - (topBlockHeight + gap)
		let bottomBlockHeight = $gameHeight - this.topSpace
		this.context.fillRect(this.xPosition, this.topSpace, this.blockWidth, bottomBlockHeight)
	}

	calculateGap(level)
	{
		return  ($playerRadius * 1.25) + (($playerRadius*7) - (($level - 1) * ($playerRadius/2.5)))
	}
}
