import { gameWidth, gameHeight } from './props.js'

let $gameWidth;
let $gameHeight;
export class Text {
	constructor(context, x, y, text, color, font)
	{

		this.context = context
		this.x = x
		this.y = y
		this.font = font || '14px Georgia'
		this.text = text || ''
		this.color = color || 'black'

		gameWidth.subscribe(v => { $gameWidth = v })
		gameHeight.subscribe(v => { $gameHeight = v })
	}
	update(text) {
		if( text )
			this.text = text
		
		this.draw()
	}
	draw() {
		this.context.font = this.font;
		this.context.fillStyle = this.color;
		this.context.fillText(this.text, this.x, this.y);
	}
}
